const MySelect = ({ options, defaultValue, value, handleSortValueChange }) => {
  return (
    <select value={value} onChange={e => handleSortValueChange(e.target.value)}>
      <option disabled value="">
        {defaultValue}
      </option>
      {options.map(option => (
        <option key={option.value} value={option.value}>
          {option.name}
        </option>
      ))}
    </select>
  );
};

export default MySelect;
